// frontend/src/pages/Home.js (CDIGO CORRIGIDO E FUNCIONAL)

import React from 'react';
import { Container, Typography, Button } from '@mui/material'; // Adicionado Button
import { useNavigate } from 'react-router-dom'; //  IMPORTANTE para o redirecionamento
import UserLogin from '../components/UserLogin';

// Importa a funo de logout do api.js
import { logout } from '../api'; 

// O componente Home deve receber a informao do usurio logado (loggedUser)
// e a funo para atualiz-lo (setLoggedUser).
function Home({ loggedUser, setLoggedUser }) { 
    const navigate = useNavigate();

    //  FUNO handleLogout 
    const handleLogout = () => {
        logout();             // Limpa o token e headers
        setLoggedUser(null);  // Limpa o estado no componente principal (App.js)
        navigate('/');        // Redireciona para a raiz ou a tela de login
    };

    // Estrutura Condicional
    return (
        <Container maxWidth="md" style={{ textAlign: 'center', marginTop: '50px' }}>
            <Typography variant="h3" gutterBottom>
                Bem-vindo ao SectorFlow
            </Typography>

            {/*  CONDIO: SE ESTIVER LOGADO, MOSTRA O CONTEDO  */}
            {loggedUser ? (
                <div>
                    <Typography variant="h5" color="primary" style={{ marginBottom: '20px' }}>
                        Ol, {loggedUser.name} ({loggedUser.role})!
                    </Typography>
                    
                    {/* Boto de Deslogar com MUI Button */}
                    <Button 
                        variant="contained" 
                        color="error" 
                        onClick={handleLogout} 
                        style={{ marginTop: '20px' }}
                    >
                        Sair / Deslogar
                    </Button>
                    
                    {/* Aqui voc colocaria links ou dashboard para usurios logados */}
                    {/* Ex: <SectorList /> */}
                </div>
            ) : (
                //  CONDIO: SE NO ESTIVER LOGADO, MOSTRA A TELA DE LOGIN 
                <UserLogin setLoggedUser={setLoggedUser} />
            )}
        </Container>
    );
}

export default Home;