// frontend/src/pages/UsuariosPage.jsx (Com boto Voltar e correo de robustez)

import React, { useState, useEffect, useCallback } from 'react';
import { Container, Typography, Grid, Dialog, DialogTitle, DialogContent, Button, Box } from '@mui/material'; //  IMPORTADO: Button e Box
import UserForm from '../components/UserForm';
import UserList from '../components/UserList';
import API from '../api';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom'; //  IMPORTADO: useNavigate

// O componente agora deve receber a role
function UsuariosPage({ userRole }) {
    const [users, setUsers] = useState([]);
    const [sectors, setSectors] = useState([]);

    // Estados para a Modal de Edio
    const [openModal, setOpenModal] = useState(false);
    const [editingUser, setEditingUser] = useState(null);

    //  HOOK DE NAVEGAO 
    const navigate = useNavigate();

    //  CORREO DE ROBUSTEZ: Usa toUpperCase() 
    const canManageUsers = userRole && userRole.toUpperCase() === 'ADMIN';

    // Refatora a busca para incluir tratamento de erro e usar useCallback
    const fetchUsers = useCallback(async () => {
        // Se no for ADMIN, no faz a chamada (o backend bloquearia, mas evitamos o erro)
        if (!canManageUsers) {
            setUsers([]); // Garante que a lista est vazia
            return;
        }

        try {
            const res = await API.get('/users');
            setUsers(res.data);
        } catch (error) {
            console.error('Erro ao buscar usurios:', error);
            toast.error('No foi possvel carregar a lista de usurios. Permisso negada.');
        }
    }, [canManageUsers]); // Depende de canManageUsers

    const fetchSectors = useCallback(async () => {
        try {
            const res = await API.get('/sectors');
            setSectors(res.data);
        } catch (error) {
            console.error('Erro ao buscar setores:', error);
            toast.error('No foi possvel carregar a lista de setores para formulrio.');
        }
    }, []);

    useEffect(() => {
        fetchUsers();
        // A busca de setores deve ocorrer sempre que a pgina carregar
        fetchSectors();
    }, [fetchUsers, fetchSectors]);

    // Lgica da Modal
    const handleEditClick = (user) => {
        setEditingUser(user);
        setOpenModal(true);
    };

    const handleCloseModal = () => {
        setOpenModal(false);
        setEditingUser(null);
        fetchUsers(); // Recarrega a lista aps fechar (seja por criao ou edio)
    };

    // Lgica de Deleo
    const handleDeleteUser = async (id) => {
        try {
            await API.delete(`/users/${id}`);
            fetchUsers();
            toast.success('Usurio deletado com sucesso!');
        } catch (error) {
            toast.error(error.response?.data?.error || 'Erro ao deletar usurio. Permisso insuficiente.');
        }
    };

    // ----------------------------------------------------
    // RENDERIZAO CONDICIONAL DA TELA
    // ----------------------------------------------------
    if (!canManageUsers) {
        return (
            <Container maxWidth="md" style={{ marginTop: '50px', textAlign: 'center' }}>
                <Typography variant="h4" color="error" gutterBottom>
                    Acesso Negado
                </Typography>
                <Typography variant="h6">
                    Voc no tem permisso de administrador para gerenciar usurios.
                </Typography>
            </Container>
        );
    }

    // Se for ADMIN, renderiza a tela de Gerenciamento completa
    return (
        <Container maxWidth="lg" style={{ marginTop: '30px' }}>

            {/*  TTULO E BOTO ALINHADOS */}
            <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
                <Typography variant="h4">
                    Gerenciamento de Usurios
                </Typography>
            </Box>

            <Grid container spacing={3}>
                {/* COLUNA ESQUERDA: Criao de Novo Usurio (APENAS ADMIN) */}
                <Grid item xs={12} md={6}>
                    <Typography variant="h5" gutterBottom>
                        Criar Novo Usurio
                    </Typography>
                    <UserForm
                        sectors={sectors}
                        onFinish={handleCloseModal} // Chamando a funo que fecha a modal E recarrega
                    // No passamos currentUser, ento este UserForm  para CRIAO
                    />
                </Grid>

                {/* COLUNA DIREITA: Lista de Usurios (APENAS ADMIN) */}
                <Grid item xs={12} md={6}>
                    <Typography variant="h5" gutterBottom>
                        Lista de Usurios
                    </Typography>
                    <UserList
                        users={users}
                        onDelete={handleDeleteUser}
                        onEdit={handleEditClick}
                    />
                </Grid>
            </Grid>
                <Button
                    variant="outlined"
                    color="secondary"
                    onClick={() => navigate('/dashboard')} // Navega para o Dashboard
                >
                    Voltar
                </Button>

            {/* MODAL DE EDIO (APENAS ADMIN) */}
            <Dialog open={openModal} onClose={handleCloseModal} fullWidth maxWidth="sm">
                <DialogTitle>
                    {editingUser ? 'Editar Usurio' : 'Criar Usurio'}
                </DialogTitle>
                <DialogContent>
                    <UserForm
                        sectors={sectors}
                        currentUser={editingUser}
                        onFinish={handleCloseModal}
                    />
                </DialogContent>
            </Dialog>
        </Container>
    );
}

export default UsuariosPage;