// frontend/src/components/ProtectedRoute.js
import React from 'react';
import { Navigate } from 'react-router-dom';

// Componente que exige que o usurio esteja logado
const ProtectedRoute = ({ children, loggedUser }) => {
    // Se o usurio NO estiver logado, redireciona para a pgina inicial
    if (!loggedUser) {
        // Redireciona o usurio para a rota de login
        return <Navigate to="/" replace />; 
    }

    // Se o usurio estiver logado, renderiza o componente filho (a pgina)
    return children;
};

export default ProtectedRoute;