import React, { useState, useEffect } from 'react';
import { TextField, Button, MenuItem, Paper } from '@mui/material';
import API from '../api';
import { toast } from 'react-toastify';

function UserForm({ sectors, currentUser, onFinish }) { 
    // ----------------------------------------------------
    // 1. ESTADOS: Adicionado o estado 'role'
    // ----------------------------------------------------
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [sectorId, setSectorId] = useState('');
    const [role, setRole] = useState(''); //  NOVO ESTADO PARA A ROLE 

    // Opes de Role (Funo)
    const roleOptions = [
        { value: 'ADMIN', label: 'Administrador' },
        { value: 'VENDEDOR', label: 'Vendedor' },
        { value: 'USER', label: 'Usurio Padro' },
    ];

    // ----------------------------------------------------
    // 2. EFEITO para preencher os dados de EDIO
    // ----------------------------------------------------
    useEffect(() => {
        if (currentUser) {
            setName(currentUser.name);
            setEmail(currentUser.email);
            setPassword(''); 
            setSectorId(currentUser.sectorId || ''); 
            setRole(currentUser.role || ''); //  Carrega a ROLE do usurio em edio 
        } else {
            // Caso contrrio (modo Criao), garante que os campos esto limpos
            setName('');
            setEmail('');
            setPassword('');
            setSectorId('');
            setRole(''); // Limpa a role
        }
    }, [currentUser]); 

    // ----------------------------------------------------
    // 3. FUNO de SUBMISSO (POST ou PUT)
    // ----------------------------------------------------
    const handleSubmit = async (e) => {
        e.preventDefault();
        
        const isEditing = !!currentUser;
        
        // Prepara os dados: inclui a role e o sectorId
        const dataToSend = { name, email, sectorId, role }; //  ROLE INCLUDA AQUI 
        
        // Validao bsica
        if (!name || !email || !role || !sectorId) {
            toast.error('Preencha todos os campos obrigatrios, incluindo Nome, Email, Setor e Role.');
            return;
        }

        // Senha  obrigatria na criao OU se preenchida na Edio
        if (password) {
            dataToSend.password = password;
        }

        // Validao adicional: Senha  obrigatria na criao
        if (!isEditing && !password) {
            toast.error('A senha  obrigatria para criar um novo usurio.');
            return;
        }

        try {
            if (isEditing) {
                await API.put(`/users/${currentUser.id}`, dataToSend);
                toast.success('Usurio atualizado com sucesso!');
            } else {
                await API.post('/users', dataToSend);
                toast.success('Usurio criado com sucesso!');
            }

            onFinish(); 
            
        } catch (error) {
            const defaultMessage = isEditing ? 'Erro ao atualizar usurio.' : 'Erro ao criar usurio.';
            const errorMessage = error.response?.data?.error || defaultMessage;
            toast.error(errorMessage);
        }
    };

    // ----------------------------------------------------
    // 4. RENDERIZAO
    // ----------------------------------------------------
    const submitButtonText = currentUser ? 'Atualizar Usurio' : 'Adicionar Usurio';
    const passwordRequired = !currentUser; 

    return (
        <Paper data-testid="user-form">
            <form onSubmit={handleSubmit}>
                <TextField label="Nome" value={name} onChange={(e) => setName(e.target.value)} required fullWidth margin="normal" />
                <TextField label="Email" value={email} onChange={(e) => setEmail(e.target.value)} required fullWidth margin="normal" />
                
                {/* Campo de Seleo de Role */}
                <TextField 
                    select 
                    label="Role (Funo)" 
                    value={role} 
                    onChange={(e) => setRole(e.target.value)} 
                    required 
                    fullWidth 
                    margin="normal"
                >
                    {roleOptions.map(option => (
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextField>

                {/* Campo de Seleo de Setor */}
                <TextField select label="Setor" value={sectorId} onChange={(e) => setSectorId(e.target.value)} required fullWidth margin="normal">
                    {sectors.map(s => <MenuItem key={s.id} value={s.id}>{s.name}</MenuItem>)}
                </TextField>

                {/* Campo de Senha */}
                <TextField 
                    label={currentUser ? "Nova Senha (Opcional)" : "Senha"} 
                    type="password" 
                    value={password} 
                    onChange={(e) => setPassword(e.target.value)} 
                    required={passwordRequired} 
                    fullWidth 
                    margin="normal" 
                />
                
                <Button type="submit" variant="contained" color="primary" sx={{ mt: 2 }}>
                    {submitButtonText}
                </Button>
            </form>
        </Paper>
    );
}

export default UserForm;